import React from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import "./TopPage.css";
import Header from "../components/Header/Header";
import Product1 from "../components/Product/Product1";

const TopPage = () => {
  const navigate = useNavigate();

  const goToProductList = () => {
    navigate("/ProductList");
  };

  const goToGiftnavigator = () => {
    navigate("/giftnavigator");
  };

  return (
    <div className="top-page">
      <header>
        <Header />
      </header>

      <main>
        <section className="intro">
          <div className="visual_banner">
            <br />
            <img
              src={`${process.env.PUBLIC_URL}/images/visual.png`}
              alt="/"
            />
            <br />
          </div>
          <div>
            <p>
              
              <br />
              
            </p>
            <p>
              
              <br />
               
              <br />
              
              <br />
              
              <br />
              
            </p>
            <div className="origin">
              <p>
                
                <br />
                
                <br />
                
              </p>
              <p>
                <img
                  src={`${process.env.PUBLIC_URL}/images/logo.png`}
                  alt=""
                  style={{ width: "160px" }}
                />
              </p>
              <p></p>
            </div>
          </div>
        </section>

        <section className="new-product">
          <h3></h3>
          <Product1 />
          <button onClick={goToProductList}></button>
        </section>

        <section className="gift-search">
          <h3></h3>
          <p>
            (OK)
            <br />
            
            <br />
            
          </p>
          <button onClick={goToGiftnavigator}></button>
        </section>
      </main>

      <footer>
        <nav>
          <ul>
            <li>
              <Link to="/mypage"></Link>
            </li>
            <li>
              <Link to="/ProductList"></Link>
            </li>
            <li>
              <Link to="/giftnavigator"></Link>
            </li>
            <li>
              <Link to="/cart"></Link>
            </li>
            <li>
              <Link to="/about"></Link>
            </li>
            <li>
              <Link to="/logout"></Link>
            </li>
          </ul>
        </nav>
        <p>SNS | </p>
      </footer>
    </div>
  );
};

export default TopPage;
