import React from "react";
import "./Product1Detail.css";
import Header from "../../components/Header/Header";
import axios from "axios";
import { useContext } from "react";
import { CartContext } from "../../contexts/CartContext.jsx";

const Product4Detail = () => {
  const { handleAddToCart } = useContext(CartContext);

  const product = {
    id: "product4",
    name: "",
    description: " /  / ",
    price: 3300,
    material: ["", "", "", ""],
    size: ["317mm19mm", "47mm"],
    notes: [
      "3()",
      "",
    ],
    tags: ["", "~4000"],
    product_url: "/product/product4",
    image_url: "/images/product4.jpg",
  };

  const handleClick = async () => {
    await handleAddToCartServer();
    handleAddToCart(product);
  };

  const handleAddToCartServer = async () => {
    const token = localStorage.getItem("token");
    try {
      await axios.post(
        "https://portfolio-backend-nm0n.onrender.com/purchase/cart",
        {
          itemId: product.id,
          quantity: 1,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      alert("!");
      handleAddToCart(product);
    } catch (error) {
      console.error(":", error);
      alert("");
    }
  };

  return (
    <>
      <header>
        <Header />
      </header>
      <div className="detail-container">
        <div className="product-section">
          <img
            src={`${process.env.PUBLIC_URL}${product.image_url}`}
            alt={product.name}
          />
          <div className="text-section">
            <h2>{product.name}</h2>
            <p className="description">{product.description}</p>
            <p className="price">{product.price}</p>
            <button onClick={() => handleClick(product)}></button>
            <div className="story">
              <p>
                
                <br />
                3
              </p>
              <p>
                
                <br />
                2way
              </p>
              <p>
                
              </p>
            </div>
          </div>

          <div className="recommend">
            <h3>!</h3>
            <h4></h4>
            <p>
              33!
            </p>
          </div>

          <div className="extra-info">
            <h3></h3>
            <ul>
              {product.size.map((s, i) => (
                <li key={i} className="extra-info">
                  {s}
                </li>
              ))}
            </ul>
            <h3></h3>
            <ul>
              {product.notes.map((n, i) => (
                <li key={i}>{n}</li>
              ))}
            </ul>
          </div>
          <button onClick={handleClick}></button>
        </div>
      </div>
    </>
  );
};

export default Product4Detail;
