import React, { useState } from "react";
import axios from "axios";
import "./GiftNavigator.css";
import Header from "../Header/Header";

function GiftNavigator() {
  const [form, setForm] = useState({
    occasion: "",
    relationship: "",
    age_group: "",
    style: "",
    recipient_name: "",
    price: "",
  });
  const [gifts, setGifts] = useState([]);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSearch = async () => {
    const selectedTags = Object.values(form)
      .filter((v, key) => key !== "recipient_name" && v !== "")
      .flatMap((tag) => tag.split(""));

    if (selectedTags.length === 0) {
      alert("1");
      return;
    }

    try {
      const res = await axios.post(
        "https://portfolio-backend-nm0n.onrender.com/gift/recommend",
        {
          tags: selectedTags,
        }
      );
      setGifts(Array.isArray(res.data) ? res.data : [res.data]);
    } catch (error) {
      console.error(error);
      alert("");
    }
  };

  return (
    <div className="gift-container">
      <header>
        <Header />
      </header>
      <h2></h2>
      <p>
        
      </p>

      <div className="select-group">
        <label></label>
        <select name="occasion" onChange={handleChange}>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>

      <div className="select-group">
        <label></label>
        <select name="relationship" onChange={handleChange}>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>

      <div className="select-group">
        <label></label>
        <select name="age_group" onChange={handleChange}>
          <option value=""></option>
          <option value="10">10</option>
          <option value="20">20</option>
          <option value="30">30</option>
          <option value="40">40</option>
          <option value="50">50</option>
        </select>
      </div>

      <div className="select-group">
        <label></label>
        <select name="style" onChange={handleChange}>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>

      <div className="select-group">
        <label></label>
        <select name="price" onChange={handleChange}>
          <option value=""></option>
          <option value="~3000">~3000</option>
          <option value="~4000">~4000</option>
        </select>
      </div>

      <div className="select-group">
        <label>OK</label>
        <input type="text" name="recipient_name" onChange={handleChange} />
      </div>

      <button className="search-button" onClick={handleSearch}>
        
      </button>

      {/*  */}
      {gifts.length > 0 && (
        <div className="recommendation">
          <h3>
            {form.recipient_name || ""} 
          </h3>
          {gifts.map((gift) => (
            <div key={gift.id} className="gift-card">
              <h4>{gift.name}</h4>
              <p>{gift.description}</p>
              <p>{gift.price}</p>
              <img src={gift.image_url} alt={gift.name} />
              <a
                href={gift.product_url}
                target="_blank"
                rel="noopener noreferrer"
              >
                
              </a>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default GiftNavigator;
