// LogoutPage.jsx
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./LogoutPage.css";

export default function LogoutPage({ onLogout }) {
  const navigate = useNavigate();

  useEffect(() => {
    onLogout(); // 
    navigate("/login"); // 
  }, [onLogout, navigate]);

  return <div>...</div>;
}
