import React, { useEffect, useState } from "react";
import axios from "axios";
import "./OrderCheckout.css";
import Header from "../Header/Header";
import { useNavigate } from "react-router-dom";
import apiClient from "../../api/client";

export default function OrderCheckout() {
  const [user, setUser] = useState({});
  const [cart, setCart] = useState([]);
  const [total, setTotal] = useState(0);
  const [address, setAddress] = useState("");
  const [paymentMethod, setPaymentMethod] = useState("");
  const [shippingFee] = useState(185);
  const [subtotal, setSubtotal] = useState(0);

  const onUpdateQuantity = (id, delta) => {
    setCart((prevCart) =>
      prevCart.map((item) =>
        item.id === id
          ? { ...item, quantity: Math.max(1, item.quantity + delta) }
          : item
      )
    );
  };

  const onRemoveItem = async (id) => {
    try {
      await apiClient.delete(`/purchase/cart/${id}`, {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      setCart((prevCart) => prevCart.filter((item) => item.id !== id));
    } catch (err) {
      console.error(":", err);
    }
  };

  useEffect(() => {
    console.log("API BASE:", import.meta.env.VITE_API_BASE_URL);

    apiClient
      .get("/auth/me", {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        setUser(res.data);
        setAddress(res.data.address);
      });

    apiClient
      .get("/purchase/cart", {
        withCredentials: true,
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })
      .then((res) => {
        setCart(res.data.items);
        const totalPrice = res.data.items.reduce(
          (sum, item) => sum + item.price * item.quantity,
          0
        );
        setSubtotal(totalPrice);
        setTotal(totalPrice + shippingFee);
      })
      .catch((err) => {
        console.error(":", err);
      });
  }, []);

  useEffect(() => {
    const newSubtotal = cart.reduce(
      (sum, item) => sum + item.price * item.quantity,
      0
    );
    setSubtotal(newSubtotal);
    setTotal(newSubtotal + shippingFee);
  }, [cart]);

  const navigate = useNavigate();

  const handlePurchase = async () => {
    try {
      await apiClient.post(
        "/purchase/purchase",
        {
          payment_method: paymentMethod,
          address: address,
        },

        {
          withCredentials: true,
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );

      // 
      setCart([]);
      // 
      navigate("/order-complete");
    } catch (error) {
      console.error(":", error);
    }
  };

  return (
    <>
      <header>
        <Header />
      </header>
      <div className="background-color">
        <div className="checkout-container">
          <h2></h2>
          <section>
            <div className="mypage-card">
              <h3 className="info"></h3>
              <div className="info-block">
                <label></label>
                <p>{user.username}</p>
              </div>
              <div className="info-block">
                <label></label>
                <p>{user.birthdate}</p>
              </div>
              <div className="info-block">
                <label></label>
                <p>{user.email}</p>
              </div>
              <div className="info-block">
                <label></label>
                <p>{user.address}</p>
              </div>
            </div>
          </section>

          <section>
            <h3></h3>
            <select
              value={paymentMethod}
              onChange={(e) => setPaymentMethod(e.target.value)}
            >
              <option value=""></option>
              <option value="credit"></option>
              <option value="delivery"></option>
            </select>
          </section>

          <section>
            <h3></h3>
            <div className="total-items">
              {cart.map((item, index) => (
                <div className="cart-item" key={`${item.id}-${index}`}>
                  <h2>{item.name}</h2>
                  <p>{item.price.toLocaleString()}</p>

                  <div className="quantity-controls">
                    <p>: {item.quantity}</p>
                    <button onClick={() => onUpdateQuantity(item.id, -1)}>
                      -
                    </button>
                    <span>{item.quantity}</span>
                    <button onClick={() => onUpdateQuantity(item.id, 1)}>
                      +
                    </button>
                  </div>
                  <button onClick={() => onRemoveItem(item.id)}></button>
                </div>
              ))}
              <div className="summary">
                <p>: {subtotal.toLocaleString()}</p>
                <p>: {shippingFee}</p>
                <p className="total">: {total.toLocaleString()}</p>
              </div>
            </div>
          </section>

          <button className="confirm-button" onClick={handlePurchase}>
            
          </button>
        </div>
      </div>
    </>
  );
}
